.content-14.is-style-image-right {

	.image-wrap {
		order: 1;
	}
}

// Content will be kept inside the container
.content-14.aligncenter {
	background-color: $primary;
}

// Background color will enxtend to the edge of the screen. Content will be kept inside the container.
.content-14.alignwide {
	background-color: $primary;
}

// Background color and content are extened to the edge of the screen.
.content-14.alignfull {
	background-color: $primary;
}

